import { apiGet, apiPost } from "./api";

export function getli(ye,ta=1000000000) {
    return new Promise((resolve) => {
        apiGet(`teacher/teacherlist?`, { page: ye, pagelimit:ta }).then(res => {
            resolve(res)
        })
    })
}

export function dele(id) {
    return new Promise((resolve) => {
        apiGet(`/teacher/delteacher/?id=${id}`).then(res => {
            resolve(res)
        })
    })
}

export function add(data) {
    return new Promise((resolve) => {
        apiPost(`/index/addteacher/`, data).then(res => {
            resolve(res)
        })
    })
}

export function up(data) {
    return new Promise((resolve) => {
        apiGet(`/index/upteacher`, data).then(res => {
            resolve(res)
        })
    })
}
export function sous(data) {
    return new Promise((resolve) => {
        apiGet(`teacher/getsearch`, data).then(res => {
            resolve(res)
        })
    })
}