<template>
  <div class="home">
    <div class="about">
      <template>
        <div class="demo-input-size" style="display: flex;justify-content:space-between;width: 90%;margin: 0 auto;">
          <el-input placeholder="姓名搜索" v-model="search" class="input-with-select" style="width: 40%;margin-left:50px;"
            @input="sou">
            <el-button slot="append" type="success">搜索</el-button>
          </el-input>
          <el-button type="success" @click="dialogFormVisible = true">{{ tex }}</el-button>
        </div>
        <el-dialog :visible.sync="dialogFormVisible" title="新增讲师" style="width: 1000px;margin: 0 auto;"
          :lockScroll="false">
          <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="150px" class="demo-ruleForm">
            <el-form-item label="姓名" prop="name">
              <el-input v-model="ruleForm.name" style="width: 270px;"></el-input>
            </el-form-item>
            <el-form-item label="昵称" prop="nick">
              <el-input v-model="ruleForm.nick" style="width: 270px;">
              </el-input>
            </el-form-item>
            <el-form-item label="生日" prop="birthdata">
              <el-input v-model="ruleForm.birthdata" type="date" style="width: 270px;"></el-input>
            </el-form-item>
            <el-form-item label="性别" prop="sex">
              <el-radio-group v-model="ruleForm.sex">
                <el-radio label="0">男</el-radio>
                <el-radio label="1">女</el-radio>
              </el-radio-group>
            </el-form-item>
            <el-form-item label="手机" prop="iphone">
              <el-input v-model="ruleForm.iphone" style="width: 270px;"></el-input>
            </el-form-item>
            <el-form-item label="讲师类型" prop="teachertype">
              <el-radio-group v-model="ruleForm.teachertype">
                <el-radio label="1">讲师</el-radio>
                <el-radio label="2">班主任</el-radio>
              </el-radio-group>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="submitForm(ruleForm)">立即添加</el-button>
              <el-button @click="ckbd()">取消添加</el-button>
            </el-form-item>
          </el-form>
        </el-dialog>
        <el-table :data="tableData" style="width: 90%;margin: 0 auto;margin-top: 20px;" border>
          <el-table-column align="center" label="编号" prop="id">
          </el-table-column>
          <el-table-column align="center" label="姓名" prop="name">
          </el-table-column>
          <el-table-column align="center" label="昵称" prop="nick">
          </el-table-column>
          <el-table-column align="center" label="出生日期" prop="birthdata">
          </el-table-column>
          <el-table-column align="center" label="性别" prop="sex">
          </el-table-column>
          <el-table-column align="center" label="手机号" prop="iphone">
          </el-table-column>
          <el-table-column align="center">
            <template slot="header">
              操作
            </template>
            <template slot-scope="scope">
              <el-button size="mini" @click="upset(scope.row.id)">编辑</el-button>
              <el-button size="mini" type="danger" @click="handleDelete(scope.row.id)">删除</el-button>
            </template>
          </el-table-column>
        </el-table>
        <el-dialog :visible.sync="dialogFormVisiup" title="修改讲师" style="width: 1000px;margin: 0 auto;"
          :lockScroll="false">
          <el-form :model="ruleForm2" :rules="rules" ref="ruleForm2" label-width="150px" class="demo-ruleForm">
            <el-form-item label="姓名" prop="name">
              <el-input v-model="ruleForm2.name" style="width: 270px;"></el-input>
            </el-form-item>
            <el-form-item label="性别" prop="sex">
              <el-radio-group v-model="ruleForm2.sex">
                <el-radio label="0">男</el-radio>
                <el-radio label="1">女</el-radio>
              </el-radio-group>
            </el-form-item>
            <el-form-item label="生日" prop="birthdata">
              <el-input v-model="ruleForm2.birthdata" type="date" style="width: 270px;"></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="upget()">立即修改</el-button>
              <el-button @click="ckbd2()">取消修改</el-button>
            </el-form-item>
          </el-form>
        </el-dialog>
        <div class="block">
          <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange"
            :current-page.sync="currentPage" :page-size="10" layout="total, prev, pager, next" :total="num">
          </el-pagination>
        </div>
      </template>
    </div>
  </div>
</template>

<script>
import { getli, dele, up, add, sous } from '../api/http'
export default {
  data() {
    return {
      num: 0,
      currentPage: 1,
      tableData: [],
      search: '',
      ruleForm: {
        name: '',
        nick: '',
        sex: '',
        birthdata: '',
        iphone: '',
        teachertype: '',
      },
      ruleForm2: {
        id: '',
        name: '',
        sex: '',
        birthdata: '',
      },
      rules: {
        name: [
          { required: true, message: '请输入姓名', trigger: 'blur' },
        ],
        nick: [
          { required: true, message: '请输入昵称', trigger: 'blur' },
        ],
        sex: [
          { required: true, message: '请输入性别', trigger: 'blur' },
        ],
        birthdata: [
          { required: true, message: '请输入出生日期', trigger: 'blur' },
        ],
        iphone: [
          { required: true, message: '请输入手机号', trigger: 'blur' },
        ],
      },
      xgid: '',
      tex: '添加讲师',
      dialogFormVisible: false,
      dialogFormVisiup: false,
    }
  },
  watch: {
    currentPage() {
      console.log(1);
      if (!this.search) {
        this.get(this.currentPage)
      } else {
        var data = {
          page: this.currentPage,
          name: this.search
        }
        sous(data).then(res => {
          this.tableData = res.data.data
        })
      }
    }
  },
  created() {
    this.zts()
    this.get(this.currentPage)
  },
  methods: {
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
    },
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`);
    },
    zts() {
      getli().then(res => {
        res.data.data
        console.log(res.data);
        this.num = res.data.pagecount
        console.log(this.num);
      })
    },

    get(ye, ta = 10) {
      getli(ye, ta).then(res => {
        this.tableData = res.data.data
        this.tableData.forEach(item => {
          if (item.sex == 0) {
            item.sex = '男'
          } else {
            item.sex = '女'
          }
        })
      })
    },

    handleDelete(id) {
      this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        dele(id)
          .then(res => {
            console.log(res)
            this.get()
          })
        this.$message({
          type: 'success',
          message: '删除成功!'
        });
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        });
      })
    },
    submitForm(id) {
      console.log(id);
      add(id)
        .then(res => {
          console.log(res)
          this.get()

          this.ckbd()
        })
    },
    ckbd() {
      this.ruleForm = {
        name: '',
        nick: '',
        sex: '',
        birthdata: '',
        iphone: '',
        teachertype: '',
      }
      this.dialogFormVisible = false
    },
    upset(id) {
      this.dialogFormVisiup = true
      this.ruleForm2.id = id
      var arr = this.tableData.filter(item => item.id == id)[0]
      this.ruleForm2.name = arr.name
      this.ruleForm2.sex = arr.sex
      this.ruleForm2.birthdata = arr.birthdata
    },
    upget() {
      up(this.ruleForm2)
        .then(res => {
          console.log(res)
          if (res.code == 0) {
            this.ckbd2()
          }
          this.get()
        })
    },
    ckbd2() {
      this.ruleForm2 = {
        id: '',
        name: '',
        sex: '',
        birthdata: '',
      }
      this.dialogFormVisiup = false
    },
    sou() {
      // console.log(1);
      sous({
        name: this.search
      }).then(res => {
        console.log(res);
        this.tableData = res.data.data
        this.num = res.data.pagecount
      })
    },
    // sous(){
    //   console.log(this.search);
    // }
  }
}
</script>

<style>
.home {
  width: 100%;
  height: 100%;
  text-align: center;
}

.about {
  width: 80%;
  height: 100%;
  margin: 0 auto;
  margin-top: 8%;
}
</style>